*{
	margin:0;
}
.app{
	background-color: #fafafa;
	height: 100%;
	padding-bottom: 10px;
}
.header{
	margin-bottom: 15px;
	padding: 15px;
	background-color: white;
	border-bottom: 1px solid lightgray;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: sticky;
	top: 0;
	z-index: 1;
}
.header__img{
	object-fit: contain;
}

.app__posts{
	display: flex;
	flex-direction: column;
	align-items: center;
}
.app__signup{
	display: flex;
	flex-direction: column;
}
.app__signupimg{
	object-fit: contain;
}
@media(max-width: 768px){
	body{
		width: 100vw;
	}
}