.post{
	background-color: white;
	margin-bottom: 15px;
	width: 440px;
	border: 1px solid lightgray;
	border-radius: 3px;
}

.post__header{
	display: flex;
	align-items: center;
	padding: 15px;
}
.header__name{
	padding-left: 15px;
}
.post__img{
	object-fit: contain;
	height: auto;
	width: 100%;
	border-top: 1px solid lightgray;
	border-bottom: 1px solid lightgray;
}
.post__caption{
	padding: 10px 10px;
	width: 300px;
}
.post__comment{
	display: flex;
}

.comment__text{
	padding: 5px;
	flex: 1;
}
.comment__button{
	flex: 0;
	border: none;
	color: #3f729b;
	background-color: white;
}
.comment__button:disabled{
	color: gray;
}
.comment{
	padding: 5px 15px;
	width: 300px;
	word-wrap: break-word
}
@media(max-width: 768px){
	.post{
		width: 90vw;
	}
	.post__caption{
		width: 300px;
	}
}